"use client"

import { useState } from "react"
import type { Recipe } from "@/lib/csv-parser"

interface BrowseSectionProps {
  recipes: Recipe[]
}

export default function BrowseSection({ recipes }: BrowseSectionProps) {
  const [selectedCourse, setSelectedCourse] = useState<string>("")
  const [selectedDiet, setSelectedDiet] = useState<string>("")

  const courses = [...new Set(recipes.map((r) => r.course))].filter(Boolean)
  const diets = [...new Set(recipes.map((r) => r.diet))].filter(Boolean)

  return (
    <section className="py-16 bg-background">
      <div className="max-w-7xl mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-2xl md:text-4xl font-serif font-bold text-foreground text-balance">
            Fresh, flavorful, and never boring recipes.
          </h2>
        </div>

        <div className="flex flex-col md:flex-row gap-6 justify-center items-center mb-8">
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium text-foreground">Browse by:</span>
            <select
              value={selectedCourse}
              onChange={(e) => setSelectedCourse(e.target.value)}
              className="px-4 py-2 bg-background border border-border rounded text-foreground text-sm"
            >
              <option value="">COURSE</option>
              {courses.map((course) => (
                <option key={course} value={course}>
                  {course}
                </option>
              ))}
            </select>
          </div>

          <select
            value={selectedDiet}
            onChange={(e) => setSelectedDiet(e.target.value)}
            className="px-4 py-2 bg-background border border-border rounded text-foreground text-sm"
          >
            <option value="">DIET</option>
            {diets.map((diet) => (
              <option key={diet} value={diet}>
                {diet}
              </option>
            ))}
          </select>

          <button className="px-4 py-2 bg-primary text-primary-foreground font-medium text-xs hover:bg-primary/90 transition">
            OR SEE THE LATEST →
          </button>
        </div>
      </div>
    </section>
  )
}
